<template>
    <v-container>
        <v-row justify="center">
            <v-col cols=8>
                <v-card color="gray lighten-5">
                    <v-card-title>Sign Up!</v-card-title>
                    <v-row class="mx-auto">
                        <v-col cols=12>
                            <v-text-field label="Email"></v-text-field>
                        </v-col>
                        <v-col cols=12>
                            <v-text-field label="Password" type="password"></v-text-field>
                        </v-col>
                        <v-col cols=12>
                            <v-text-field label="Confirm Password" type="password"></v-text-field>
                        </v-col>
                        <v-col>
                            <v-btn>Submit</v-btn>
                        </v-col>
                    </v-row>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
export default {
    // Prevent the user from accessing the dashboard if they are not logged in.
    beforeRouteEnter (to, from, next) {
        next(vm => {
            if (vm.$store.state.token.length > 0) {            
                next("/dashboard");
            } else {
                next();
            }
        })
    },
    computed: {
    },
    methods: {
    }
}
</script>